* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: url("./pexels-suzy-hazelwood-1329644.jpg") rgba(0, 0, 0, 0.3);

  background-blend-mode: darken;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

main {
  background:  rgba(0, 0, 0, 0.2);
  background-blend-mode: darken;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 400px;
  max-width: 800px;
  border-radius: 5rem;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top:blue 6px solid;
  border-bottom:6px solid red;
}

.title {
  font-size: 40px;
  margin: 0;
  padding: 1px 10px;
  border-radius: 20px;
  border-bottom: 3px solid #3617ff;
  border-top: 3px solid #ef0101;
  text-shadow: 2px 2px 5px black;
  color: white;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin: 25px 10px;
  text-align: center;
  color: white;
}

.dice-container {
  display: grid;
  width: 70%;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  height: 50%;
  justify-content: center;
}

.die-face {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.die-num {
  font-size: 2rem;
}

.roll-dice {
  height: 50px;
  width: 100px;
  border: none;
  border-radius: 13px;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
  border-right: 3px solid #e8ff28;
  border-left: 3px solid #00cf9f;
  transition: all ease-in-out 100ms;
} 
.roll-dice:hover{
  border-top: 2px solid #351ae0;
  border-bottom: 2px solid #ef0101;
   border-right: 0;
  border-left:   0;
}
.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}